
.green-head{
    color: black;
    font-family: sans-serif;
    padding-top: 3rem;
    
    

}
.step{
    position: absolute;
    text-align: left;
    display: inline-block;
    font-size: 20px;
    margin-left: 68px;
    padding-top: .7rem;
}
.green-text{
    font-size: 28px;
    background-color: #B5FFB4;
    display: inline-block;
    width: 100%;
    margin-left: 12rem;
    padding-left: 1.5rem;
    padding-top: .5rem;
    padding-bottom: .5rem;
    border-radius: 10px;
   
}

.footer{
    color: black;
    /* background-color: #B5FFB4; */
    font-size: 20px;
    position: absolute;
    bottom: 4rem;
    width: 100%;
}
.footer .inside-div{
    position: relative;
}
.footer .back{
    display: inline-block;
    position: absolute;
    left: 6rem;
    background-color: #616161;
    padding: 7px 20px 7px 20px;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
   
}
.footer .continue
{
    display: inline-block;
    position: absolute;
    right: 10rem;
    background-color: #B5FFB4;;
    padding: 7px 20px 7px 20px;
    border-radius: 8px;
    cursor: pointer;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
   
    
    
}
.uLine{
    text-decoration: none;
    color: black;
}

.footer .continue:hover{
    background-color: white;
    border: 2px solid #B5FFB4;;
}
.footer .back:hover{
    background-color: white;
    border: 2px solid #616161;
}